import React, { useState, useEffect, Fragment } from 'react';
import logo from './logo.svg';
import './App.css';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import CustomMaps from './CustomMaps';
import moment from 'moment';

const App = () => {
  const [userLocation, setUserLocation] = useState(undefined);
  const [errorMessage, setErrorMessage] = useState(undefined);
  const [lastUpdateTime, setLastUpdateTime] = useState(new moment());

  useEffect(() => {
    currentLocation();
    watchCurrentLocation();
  }, []);

  useEffect(() => {
    if (userLocation) {
      const message = `Latitude:${userLocation.lat} Longitude:${userLocation.lng}`
      NotificationManager.info(message, "Location", 2000);
    }
  }, [userLocation]);

  useEffect(() => {
    if (errorMessage) {
      NotificationManager.error(errorMessage, "Location", 2000);
    }
  }, [errorMessage]);

  const currentLocation = () => {
    if (window.navigator.geolocation !== undefined) {

      const success = (position) => {
        const location = {
          lat: position.coords.latitude ? position.coords.latitude : undefined,
          lng: position.coords.longitude ? position.coords.longitude : undefined,
        }

        setUserLocation(location);
      }

      const error = (error) => {
        // denied

        const errorTypes = {
          0: "不明原因錯誤",
          1: "使用者拒絕提供位置資訊",
          2: "無法取得位置資訊",
          3: "位置查詢逾時"
        };

        setErrorMessage(errorTypes[error.code])
      }

      const optoptions = {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      };

      window.navigator.geolocation.getCurrentPosition(success, error, optoptions);
    }
  }

  const watchCurrentLocation = () => {
    const success = (position) => {
      const location = {
        lat: position.coords.latitude ? position.coords.latitude : undefined,
        lng: position.coords.longitude ? position.coords.longitude : undefined,
      }

      const now = new moment();
      const duration = moment.duration(now.diff(lastUpdateTime));
      const timeSpan = duration.seconds();

      let allowUpdate = timeSpan > 20 ? true : false;

      if (allowUpdate) {
        setUserLocation(location);
      }

      console.log(location);
    }

    const error = (error) => {
      // denied

      const errorTypes = {
        0: "不明原因錯誤",
        1: "使用者拒絕提供位置資訊",
        2: "無法取得位置資訊",
        3: "位置查詢逾時"
      };

      setErrorMessage(errorTypes[error.code])
    }

    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    };

    navigator.geolocation.watchPosition(success, error, options);
  }

  return (
    <Fragment>
      <NotificationContainer />
      <div className="App">

        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
        </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
        </a>
        </header>
        <CustomMaps
          googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyAm96fO64nsRObE_bPsdv5ND9w2fgbtGEs"
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={<div style={{ height: `600px` }} />}
          mapElement={<div style={{ height: `100%` }} />}
          lat={userLocation && userLocation.lat ? userLocation.lat : 25.0339808}
          lng={userLocation && userLocation.lng ? userLocation.lng : 121.5623502}
          isMarkerShown={userLocation ? true : false}
        />
      </div>
    </Fragment>
  );
}

export default App;
